import styled from 'styled-components';

export const Wrapper = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 600px;
`;

export const Form = styled.form``;

export const Label = styled.label`
  margin: 0;
  display: flex;
  flex-direction: column;
  font-size: 2rem;
`;
