import { useLocalStore, useObserver } from 'mobx-react-lite';
import React from 'react';
import { useHistory } from 'react-router-dom';
import Input from '../../components/Input';
import { rootStore } from '../../stores';
import { Form, Label, Wrapper } from './Login.styles';

const Login: React.FC = () => {
  const history = useHistory();

  const store = useLocalStore(() => ({
    value: '',
    isKeyValid: true,

    onChange(event: React.ChangeEvent<HTMLInputElement>) {
      store.value = event.currentTarget.value;
      !store.isKeyValid && (store.isKeyValid = true);
    },

    onSubmit(event: React.FormEvent<HTMLFormElement>) {
      event.preventDefault();
      rootStore.authStore.setKey(store.value)
        ? history.push('/balance')
        : (store.isKeyValid = false);
    }
  }));

  const label = () => {
    return history.location.search.includes('403')
      ? 'You need to login before you can see your balance'
      : 'Please log in using your private key';
  };

  return useObserver(() => (
    <Wrapper>
      <Form onSubmit={store.onSubmit}>
        <Label>
          {label()}
          <Input
            value={store.value}
            onChange={store.onChange}
            hasError={!store.isKeyValid}
            autoFocus
            required
          />
        </Label>
      </Form>
    </Wrapper>
  ));
};

export default Login;
